import React, { useState } from 'react';
import { Plus, ChevronLeft } from 'lucide-react';
import { Popover, PopoverContent, PopoverTrigger } from '@/components/ui/popover';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
    Select,
    SelectContent,
    SelectItem,
    SelectTrigger,
    SelectValue,
} from '@/components/ui/select';

interface AddItemFlyoutProps {
    lists: { id: string; name: string }[];
    onAddItem: (name: string, listId: string) => void;
    onAddList: (name: string) => void;
    defaultListId?: string;
}

type FlyoutView = 'menu' | 'item' | 'list';

export const AddItemFlyout = ({ lists, onAddItem, onAddList, defaultListId }: AddItemFlyoutProps) => {
    const [open, setOpen] = useState(false);
    const [view, setView] = useState<FlyoutView>('menu');

    // Form States
    const [itemName, setItemName] = useState('');
    const [selectedListId, setSelectedListId] = useState(defaultListId || (lists[0]?.id || ''));
    const [listName, setListName] = useState('');

    // Reset state when opening/closing
    React.useEffect(() => {
        if (open) {
            setView('menu');
            setItemName('');
            setListName('');
            if (defaultListId) setSelectedListId(defaultListId);
        }
    }, [open, defaultListId]);

    const submitItem = () => {
        if (!itemName.trim() || !selectedListId) return;
        onAddItem(itemName, selectedListId);
        setOpen(false);
    };

    const submitList = () => {
        if (!listName.trim()) return;
        onAddList(listName);
        setOpen(false);
    };

    return (
        <Popover open={open} onOpenChange={setOpen}>
            <PopoverTrigger asChild>
                <Button variant="outline" size="icon" className="h-8 w-8 rounded-full border-dashed border-slate-300 text-slate-500 hover:text-[var(--accent-olive)] hover:border-[var(--accent-olive)] hover:bg-slate-50">
                    <Plus className="h-4 w-4" />
                </Button>
            </PopoverTrigger>
            <PopoverContent className="w-80 p-0" align="end">

                {/* Header / Back Button */}
                {view !== 'menu' && (
                    <div className="flex items-center gap-2 p-2 border-b border-slate-100">
                        <Button variant="ghost" size="sm" className="h-6 w-6 p-0 rounded-full" onClick={() => setView('menu')}>
                            <ChevronLeft className="h-4 w-4" />
                        </Button>
                        <span className="text-xs font-semibold uppercase tracking-wider text-slate-500">
                            {view === 'item' ? 'Add Item' : 'New List'}
                        </span>
                    </div>
                )}

                <div className="p-4">
                    {view === 'menu' && (
                        <div className="grid gap-2">
                            <Button variant="ghost" className="justify-start font-normal" onClick={() => setView('item')}>
                                <span className="mr-2">🍎</span> Add Item
                            </Button>
                            <Button variant="ghost" className="justify-start font-normal" onClick={() => setView('list')}>
                                <span className="mr-2">📝</span> Create New List
                            </Button>
                        </div>
                    )}

                    {view === 'item' && (
                        <div className="grid gap-4">
                            <div className="grid gap-2">
                                <Label htmlFor="name">Item Name</Label>
                                <Input
                                    id="name"
                                    placeholder="e.g. Milk"
                                    value={itemName}
                                    onChange={(e) => setItemName(e.target.value)}
                                    // Submit on Enter
                                    onKeyDown={(e) => { if (e.key === 'Enter') submitItem(); }}
                                    autoFocus
                                />
                            </div>
                            <div className="grid gap-2">
                                <Label htmlFor="list">List</Label>
                                <Select value={selectedListId} onValueChange={setSelectedListId}>
                                    <SelectTrigger>
                                        <SelectValue placeholder="Select a list" />
                                    </SelectTrigger>
                                    <SelectContent>
                                        {lists.map((list) => (
                                            <SelectItem key={list.id} value={list.id}>
                                                {list.name}
                                            </SelectItem>
                                        ))}
                                    </SelectContent>
                                </Select>
                            </div>
                            <Button onClick={submitItem} className="w-full bg-[var(--accent-olive)] hover:bg-[var(--accent-sage)]">
                                Add Item
                            </Button>
                        </div>
                    )}

                    {view === 'list' && (
                        <div className="grid gap-4">
                            <div className="grid gap-2">
                                <Label htmlFor="listName">List Name</Label>
                                <Input
                                    id="listName"
                                    placeholder="e.g. Trader Joe's"
                                    value={listName}
                                    onChange={(e) => setListName(e.target.value)}
                                    onKeyDown={(e) => { if (e.key === 'Enter') submitList(); }}
                                    autoFocus
                                />
                            </div>
                            <Button onClick={submitList} className="w-full bg-[var(--accent-olive)] hover:bg-[var(--accent-sage)]">
                                Create List
                            </Button>
                        </div>
                    )}
                </div>
            </PopoverContent>
        </Popover>
    );
};
